body {
  font-size: 16px;
}
.Utility__Container {
  *zoom: 1;
  max-width: 1180px;
  margin-left: auto;
  margin-right: auto;
}
.Utility__Container:before{
    content: '';
    display: table;
}
.Utility__Container:after{
    content: '';
    display: table;
    clear: both;
}
.SiteHeader {
  background-color: #f55247;
  border-bottom: 1px solid #808080;
}
.SiteHeader__Inner {
  *zoom: 1;
  max-width: 1180px;
  margin-left: auto;
  margin-right: auto;
}
.SiteHeader__Inner:before{
    content: '';
    display: table;
}
.SiteHeader__Inner:after{
    content: '';
    display: table;
    clear: both;
}
.SiteHeader__Inner__SiteTitle {
  width: calc(99.99% * 3/12 - (30px - 30px * 3/12));
  margin: 0;
  color: #fdfdfd;
  padding: 20px 0;
}
.SiteHeader__Inner__SiteTitle:nth-child(n){
    float: left;
    margin-right: 30px;
    clear: none;
}
.SiteHeader__Inner__SiteTitle:last-child{
    margin-right: 0;
}
.SiteHeader__Inner__SiteTitle:nth-child(12n){
    margin-right: 0;
}
.SiteHeader__Inner__SiteTitle:nth-child(12n + 1){
    clear: left;
}
.SiteHeader__Inner__Nav {
  width: calc(99.99% * 9/12 - (30px - 30px * 9/12));
}
.SiteHeader__Inner__Nav:nth-child(n){
    float: left;
    margin-right: 30px;
    clear: none;
}
.SiteHeader__Inner__Nav:last-child{
    margin-right: 0;
}
.SiteHeader__Inner__Nav:nth-child(12n){
    margin-right: 0;
}
.SiteHeader__Inner__Nav:nth-child(12n + 1){
    clear: left;
}
.SiteHeader__Inner__Nav__Links {
  margin: 0;
  float: right;
}
.SiteHeader__Inner__Nav__Links__Link {
  display: inline-block;
  padding: 2rem 1rem;
  position: relative;
}
.SiteHeader__Inner__Nav__Links__Link > a {
  padding: 2rem 0px;
  color: #fff;
  text-decoration: none;
}
.SiteHeader__Inner__Nav__Links__Link > a:hover {
  text-decoration: underline;
}
.SiteHeader__Inner__Nav__Links__Link__Sub {
  padding: 0.3rem 1rem 1em 1em;
  position: absolute;
  top: 84px;
  left: 0;
  width: 150px;
  box-shadow: none;
  display: none;
  opacity: 0;
  visibility: hidden;
  background: #f55247;
  border: 1px solid #808080;
  border-top: 0;
}
.SiteHeader__Inner__Nav__Links .SiteHeader__Inner__Nav__Links__Link:hover .SiteHeader__Inner__Nav__Links__Link__Sub {
  display: block;
  opacity: 1;
  visibility: visible;
}
.SiteHeader__Inner__Nav__Links__Link__Sub__Link {
  background: #f55247;
  display: block;
  color: #fff;
  text-shadow: 0 -1px 0 #000;
}
.SiteHeader__Inner__Nav__Links__Link__Sub__Link > a {
  color: #fff;
  text-decoration: none;
}
.SiteHeader__Inner__Nav__Links__Link__Sub__Link > a:hover {
  text-decoration: underline;
}
.Forums_ChosenCategory {
  padding: 3em;
  lost-position: middle-center;
}
.Forums_ChosenCategory a {
  color: #fff;
}
.Forums_TopicHeader {
  padding: 1.5em;
  lost-position: middle-center;
  text-align: center;
}
.Forums_TopicHeader a {
  color: #fff;
}
.Forums__Content {
  margin-top: 2rem;
  margin-left: calc(99.99% * (-1/20 * -1) - (30px - 30px * (-1/20 * -1)) + 30px) !important;
  width: calc(99.99% * 15/20 - (30px - 30px * 15/20));
}
.Forums__Content:nth-child(n){
    float: left;
    margin-right: 30px;
    clear: none;
}
.Forums__Content:last-child{
    margin-right: 0;
}
.Forums__Content:nth-child(20n){
    margin-right: 0;
}
.Forums__Content:nth-child(20n + 1){
    clear: left;
}
.Forums__Paginator {
  margin-top: 2rem;
}
.Forums__Paginator ul {
  padding: 0;
  margin: 0;
  text-align: center;
}
.Forums__Paginator li {
  list-style: none;
  display: inline-block;
}
.Forums__Paginator li a {
  display: inline-block;
  margin-bottom: 0;
  text-align: center;
  vertical-align: middle;
  cursor: pointer;
  white-space: nowrap;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  background: #f55247;
  color: #fff;
  padding: 0.3rem 1rem;
  font-weight: bold;
  text-decoration: none;
}
.Forums__Paginator li a:hover {
  color: #fff;
}
.Forums__Paginator li.disabled {
  display: none;
}
.Forums__Sidebar {
  margin-top: 2rem;
  width: calc(99.99% * 4/20 - (30px - 30px * 4/20));
  padding: gutter(1);
}
.Forums__Sidebar:nth-child(n){
    float: left;
    margin-right: 30px;
    clear: none;
}
.Forums__Sidebar:last-child{
    margin-right: 0;
}
.Forums__Sidebar:nth-child(20n){
    margin-right: 0;
}
.Forums__Sidebar:nth-child(20n + 1){
