}
.Forums__Sidebar__CategoriesList {
  margin: 1rem 0 0 0;
  padding: 0;
}
.Forums__Sidebar__CategoriesList__Item {
  display: block;
  text-align: left;
  list-style: none;
  font-size: 1rem;
  padding: 0.2rem 0;
}
.Forums__Sidebar__CategoriesList__Item a {
  color: #000;
  text-decoration: none;
}
.Forums__Sidebar__CategoriesList__Item a:hover {
  color: #f55247;
}
.Forums__Sidebar__CategoriesList__Item__Color {
  display: inline-block;
  border-radius: 5px;
  width: 1rem;
  height: 1rem;
  margin-right: 0.5rem;
}
.Forums__Sidebar__CategoriesList__Item--All:before {
  content: ' ';
  display: inline-block;
  border-radius: 5px;
  width: 1rem;
  height: 1rem;
  margin-right: 0.5rem;
  border: 1px solid #a9a9a9;
}
.Forums__CategoryLabel {
  display: inline-block;
  margin-bottom: 0;
  text-align: center;
  vertical-align: middle;
  cursor: pointer;
